@import url('https://fonts.googleapis.com/css2?family=Nanum+Brush+Script&family=Noto+Serif+KR:wght@500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Caveat&display=swap');
@font-face {
  font-family: 'GangwonEdu_OTFLightA';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2201-2@1.0/GangwonEdu_OTFLightA.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'GangwonEdu_OTFBoldA';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2201-2@1.0/GangwonEdu_OTFBoldA.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

body {
  background-color: #FFFFFF;
  text-align: center;
  align-items: center;
  font-size: 20px;
  font-family: 'GangwonEdu_OTFLightA';
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.CloseButton {
  padding: 15px;
  height: 25px;
  float: right;
  opacity: 0.6;
}

.CloseButton:active {
  opacity: 1;
}

.CloseButton:hover {
  cursor: pointer;
}

.Daughter {
  padding-left: 6px;
  padding-right: 6px;
  text-align: center;
}

.FlowerIcon {
  height: 100px;
}

.Footer {
  /*background-color: floralwhite;*/
  background-color: #EBD7D6;
  color: #685C5D;
  font-size: 15px;
  padding-top: 1vh;
  padding-bottom: 1vh;
}

.Gallery {
  padding-left: 5%;
  padding-right: 5%;
  padding-top: 2vh;
  padding-bottom: 2vh;
}

.GreetingImage {
  vertical-align: center;
}

.GreetingMessage {
  vertical-align: center;
  padding-top: 3vh;
  padding-bottom: 3vh;
  line-height: 1.5;
  background-color: #E4CBCA;
}

.GreetingSnap {
  width: 100%;
}

.Image {
  max-height: 100%;
  max-width: 100%;
  height: auto;
  width: auto;
  vertical-align: middle;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  display: none;
}

.ImageSlide {
  height: 100%;
}

.ImageSlideItem {
  height: calc(95% - 45px);
  width: 100%;
  position: relative;
  float: left;
}

.ImageViewWindow {
  display: none;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: auto;
  background-color: rgba(0,0,0,0.90);
}

.Location {
  clear: both;
  vertical-align: center;
  padding-top: 2vh;
  padding-bottom: 3vh;
}

.LocationDetails {
  padding-top: 1vh;
}

.LocationMap {
  display: flex;
  justify-content: center;
  height: 40vh;
  padding-top: 5vh;
}

.NextButton {
  width: 30px;
  height: 30px;
  opacity: .9;
}

.NextButton:active {
  opacity: 1;
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.NextButtonPanel {
  right: 0;
}

.PrevButton {
  width: 30px;
  height: 30px;
  opacity: .9;
}

.PrevButton:active {
  opacity: 1;
  -webkit-tap-highlight-color: transparent;
}

.PrevButtonPanel {
  left: 0;
}

.PrevButtonPanel, .NextButtonPanel {
  margin-top: 45px;
  height: calc(100% - 45px);
  width: 10%;
  float: left;
  position: absolute;
  top: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.ShowMoreButton {
  margin-top: 1vh;
  border: 1px solid rgba(232, 198, 203, 0.6);
  width: 100%;
  height: 40px;
  background-color: #FFFFFF;
  color: #D47C88;
  font-size: 18px;
  line-height: 40px;
  font-family: 'GangwonEdu_OTFLightA';
}

.ShowMoreButton:active {
  background-color: #F3E4E6;
}

.ShowMoreButton:hover {
  cursor: pointer;
}

.SonDaughter {
  font-size: 15px;
}

.Splitter {
  /*background-color: #E7EDF0;*/
  background-color: #FFFFFF;
  /*background-color: floralwhite;*/
  min-height: 3vh;
  padding-top: 1vh;
  padding-bottom: 1vh;
}

.TextArea {
  color: #685C5D;
  font-size: 18px;
  line-height: 1.6;
}

.Thumbnail {
  position: relative;
  width: 100%;
  height: 100%;
}

.ThumbnailItem {
  display: inline;
  position: relative;
  float: left;
  width: 33%;
  /*box-sizing: border-box;*/
  padding-right: 0.15%;
  padding-left: 0.15%;
  padding-bottom: 1.5px;
}

.ThumbnailItem:hover {
  cursor: pointer;
}

.ThumbnailTable {
  display: inline-block;
  /*position: relative;*/
  line-height: 0;
}

.ThumbnailWrapper {
  position: relative;
  overflow: hidden;
}

.TitleEng {
  color: #D47C88;
  font-family: "Caveat";
  font-size: 36px;
  height: 18px;
}

.TitleKor {
  color: #C1B3B4;
  font-size: 22px;
}

.TitleUnderline {
  border: 0;
  height: 1px;
  width: 20vh;
  color: #F3E4E6;
  background-color: #F3E4E6;
  opacity: 0.6;
}

.Transportation {
  padding-top: 1vh;
}

.TransportationType {
  color: #D49C7C;
  font-size: 18px;
}